<script>
import { Search, Swipe, SwipeItem, Tabbar, TabbarItem, Icon } from "vant";
import axios from "axios";
export default {
  components: {
    [Search.name]: Search,
    [Swipe.name]: Swipe,
    [Tabbar.name]: Tabbar,
    [Icon.name]: Icon,
    [TabbarItem.name]: TabbarItem,
    [SwipeItem.name]: SwipeItem
  },
  data() {
    return {
      active: 0
    };
  },
  mounted() {
    axios
      .post(process.env.VUE_APP_API + "queryHotRecommendList.do", {
        activityId: 190822080,
        page: this.page++,
        rows: 5
      })
      .then(response => {
        console.log("response", response);
      })
      .catch(function(error) {
        console.log(error);
      });
  },
